import React, { Dispatch, SetStateAction, SyntheticEvent } from "react"
import {
  DocumentsCollectionAudioItem,
  DocumentsCollectionEventInfo,
  DocumentsCollectionRPJSONItem,
} from "@rp/react/tanstack-api/documents"
import { ChevronRightSolidIcon } from "@rp/react/web/icons"
import { Box, Tabs, Typography } from "@rp/react/web/mui/core"
import { Apartment } from "@rp/react/web/mui/icons"
import { DocumentsCollectionAudioButton } from "../DocumentsCollectionAudioButton/DocumentsCollectionAudioButton"
import { DocumentsCollectionTab } from "../DocumentsCollectionTab"

type DocumentsCollectionHeaderProps = {
  eventInfo: DocumentsCollectionEventInfo | null
  audioItem: DocumentsCollectionAudioItem | null
  tabIdx: number
  setTabIdx: Dispatch<SetStateAction<number>>
  visualItems: Array<DocumentsCollectionRPJSONItem> | null
  isFromChat: boolean
}

const DocumentsCollectionHeader = ({
  eventInfo,
  setTabIdx,
  audioItem,
  tabIdx,
  visualItems,
  isFromChat,
}: DocumentsCollectionHeaderProps) => {
  const handleChange = (_e: SyntheticEvent, newValue: number) => setTabIdx(newValue)

  if (!eventInfo) return null
  return (
    <Box
      sx={{
        position: "sticky",
        width: "100%",
        backgroundColor: "surface.background",
        zIndex: 8,
        top: isFromChat ? "56px" : 0,
      }}
    >
      <Box
        display="flex"
        alignItems="center"
        borderBottom="1px solid #FFFFFF29"
        padding="9.5px 16px"
      >
        {eventInfo.entity.logoUrl ? (
          <img
            src={eventInfo.entity.logoUrl}
            alt={eventInfo.entity.name}
            height="20px"
            width="20px"
          />
        ) : (
          <Apartment sx={{ height: "18px", width: "18px", marginTop: "-2px" }} />
        )}

        <Typography fontWeight={700} sx={{ ml: "6px" }}>
          {eventInfo.entity.name}
        </Typography>

        <Typography color="text.secondary" fontSize="14px" sx={{ ml: "4px", mt: "2px" }}>
          {eventInfo.entity.ticker}
        </Typography>

        <ChevronRightSolidIcon color="text.200" style={{ margin: "0 12px" }} />

        <Typography>{eventInfo.event}</Typography>
      </Box>

      <Box
        sx={{
          height: "24px",
          minHeight: "36px",
          marginTop: "9.5px",
          paddingLeft: "8px",
          paddingBottom: "12px",
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          paddingRight: 2,
          borderBottom: "1px solid #FFFFFF29",
        }}
      >
        <Tabs
          value={tabIdx}
          onChange={handleChange}
          sx={{
            height: "24px",
            minHeight: "36px",
            marginTop: "12px",
            paddingBottom: "12px",
            "& .MuiButtonBase-root": {
              minWidth: 5,
              textTransform: "none",
              my: 0,
              mx: "8px",
              padding: 0,
            },
          }}
        >
          {visualItems?.map(DocumentsCollectionTab)}
        </Tabs>

        <DocumentsCollectionAudioButton audioItem={audioItem} eventInfo={eventInfo} />
      </Box>
    </Box>
  )
}

export { DocumentsCollectionHeader }
